import React, { useState } from 'react';

const InputForm = ({ onChange, inputValues }) => {
  const [subscriptionsEnabled, setSubscriptionsEnabled] = useState(false);
  const [contractsEnabled, setContractsEnabled] = useState(false);

  const handleChange = (section, key, value) => {
    onChange(section, { [key]: value });
  };

  return (
    <div className="space-y-4">
      <div>
        <h2 className="text-xl font-bold">Initial Investment</h2>
        <input type="number" placeholder="Vehicle" onChange={(e) => handleChange('initialInvestment', 'vehicle', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Treadmills" onChange={(e) => handleChange('initialInvestment', 'treadmills', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Branding" onChange={(e) => handleChange('initialInvestment', 'branding', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Website" onChange={(e) => handleChange('initialInvestment', 'website', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Booking Software" onChange={(e) => handleChange('initialInvestment', 'bookingSoftware', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Other" onChange={(e) => handleChange('initialInvestment', 'other', e.target.value)} className="border p-2 w-full"/>
        <div className="text-right font-bold">Total Initial Investment: ${parseFloat(inputValues.initialInvestment.vehicle || 0) + parseFloat(inputValues.initialInvestment.treadmills || 0) + parseFloat(inputValues.initialInvestment.branding || 0) + parseFloat(inputValues.initialInvestment.website || 0) + parseFloat(inputValues.initialInvestment.bookingSoftware || 0) + parseFloat(inputValues.initialInvestment.other || 0)}</div>
      </div>
      <div>
        <h2 className="text-xl font-bold">Estimated Monthly Operational Cost</h2>
        <input type="number" placeholder="Fuel" onChange={(e) => handleChange('operationalCosts', 'fuel', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Labor" onChange={(e) => handleChange('operationalCosts', 'labor', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Insurance" onChange={(e) => handleChange('operationalCosts', 'insurance', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Maintenance" onChange={(e) => handleChange('operationalCosts', 'maintenance', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Software" onChange={(e) => handleChange('operationalCosts', 'software', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Vehicle Loan" onChange={(e) => handleChange('operationalCosts', 'vehicleLoan', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Other Loans" onChange={(e) => handleChange('operationalCosts', 'otherLoans', e.target.value)} className="border p-2 w-full"/>
        <div className="text-right font-bold">Total Monthly Operational Cost: ${parseFloat(inputValues.operationalCosts.fuel || 0) + parseFloat(inputValues.operationalCosts.labor || 0) + parseFloat(inputValues.operationalCosts.insurance || 0) + parseFloat(inputValues.operationalCosts.maintenance || 0) + parseFloat(inputValues.operationalCosts.software || 0) + parseFloat(inputValues.operationalCosts.vehicleLoan || 0) + parseFloat(inputValues.operationalCosts.otherLoans || 0)}</div>
        <div className="text-right">Weekly Estimate: ${(parseFloat(inputValues.operationalCosts.fuel || 0) + parseFloat(inputValues.operationalCosts.labor || 0) + parseFloat(inputValues.operationalCosts.insurance || 0) + parseFloat(inputValues.operationalCosts.maintenance || 0) + parseFloat(inputValues.operationalCosts.software || 0) + parseFloat(inputValues.operationalCosts.vehicleLoan || 0) + parseFloat(inputValues.operationalCosts.otherLoans || 0)) / 4.33}</div>
      </div>
      <div>
        <h2 className="text-xl font-bold">Revenue Streams (Monthly)</h2>
        <input type="number" placeholder="Individual Sessions" onChange={(e) => handleChange('revenueStreams', 'individualSessionPrice', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Number of Customers" onChange={(e) => handleChange('revenueStreams', 'individualSessionCustomers', e.target.value)} className="border p-2 w-full"/>
        <div className="text-right">Weekly Revenue: ${(parseFloat(inputValues.revenueStreams.individualSessionPrice || 0) * parseFloat(inputValues.revenueStreams.individualSessionCustomers || 0)) / 4.33}</div>
        <label className="block">
          <input type="checkbox" checked={subscriptionsEnabled} onChange={(e) => { setSubscriptionsEnabled(e.target.checked); handleChange('revenueStreams', 'subscriptionsEnabled', e.target.checked); }} className="mr-2"/>
          Enable Subscriptions
        </label>
        {subscriptionsEnabled && (
          <>
            <input type="number" placeholder="Subscription Price" onChange={(e) => handleChange('revenueStreams', 'subscriptionPrice', e.target.value)} className="border p-2 w-full"/>
            <input type="number" placeholder="Number of Subscribers" onChange={(e) => handleChange('revenueStreams', 'subscriptionCustomers', e.target.value)} className="border p-2 w-full"/>
            <input type="number" placeholder="Avg Sessions with Subscription" onChange={(e) => handleChange('revenueStreams', 'subscriptionSessions', e.target.value)} className="border p-2 w-full"/>
            <input type="number" placeholder="Avg Session Cost with Subscription" onChange={(e) => handleChange('revenueStreams', 'subscriptionSessionCost', e.target.value)} className="border p-2 w-full"/>
            <div className="text-right">Total Monthly Subscription Revenue: ${parseFloat(inputValues.revenueStreams.subscriptionPrice || 0) * parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0) + parseFloat(inputValues.revenueStreams.subscriptionSessionCost || 0) * parseFloat(inputValues.revenueStreams.subscriptionSessions || 0) * parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0)}</div>
          </>
        )}
        <label className="block">
          <input type="checkbox" checked={contractsEnabled} onChange={(e) => { setContractsEnabled(e.target.checked); handleChange('revenueStreams', 'contractsEnabled', e.target.checked); }} className="mr-2"/>
          Enable Contracts
        </label>
        {contractsEnabled && (
          <>
            <input type="number" placeholder="Contract Price" onChange={(e) => handleChange('revenueStreams', 'contractPrice', e.target.value)} className="border p-2 w-full"/>
            <input type="number" placeholder="Number of Contracts" onChange={(e) => handleChange('revenueStreams', 'contractCustomers', e.target.value)} className="border p-2 w-full"/>
            <div className="text-right">Total Monthly Contract Revenue: ${parseFloat(inputValues.revenueStreams.contractPrice || 0) * parseFloat(inputValues.revenueStreams.contractCustomers || 0)}</div>
          </>
        )}
        <div className="text-right font-bold">Total Monthly Revenue: ${parseFloat(inputValues.revenueStreams.individualSessionPrice || 0) * parseFloat(inputValues.revenueStreams.individualSessionCustomers || 0) + (subscriptionsEnabled ? parseFloat(inputValues.revenueStreams.subscriptionPrice || 0) * parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0) + parseFloat(inputValues.revenueStreams.subscriptionSessionCost || 0) * parseFloat(inputValues.revenueStreams.subscriptionSessions || 0) * parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0) : 0) + (contractsEnabled ? parseFloat(inputValues.revenueStreams.contractPrice || 0) * parseFloat(inputValues.revenueStreams.contractCustomers || 0) : 0)}</div>
      </div>
    </div>
  );
};

export default InputForm;
