import React, { useState } from 'react';
import InputForm from './InputForm';
import Results from './Results';

const Calculator = () => {
  const [inputValues, setInputValues] = useState({
    initialInvestment: {
      vehicle: 0,
      treadmills: 0,
      branding: 0,
      website: 0,
      bookingSoftware: 0,
      other: 0,
    },
    operationalCosts: {
      fuel: 0,
      labor: 0,
      insurance: 0,
      maintenance: 0,
      software: 0,
      vehicleLoan: 0,
      otherLoans: 0,
    },
    revenueStreams: {
      individualSessions: 0,
      individualCustomers: 0,
      subscriptionsEnabled: false,
      subscriptionPrice: 0,
      subscriptionCustomers: 0,
      avgSessionWithSubscription: 0,
      avgCostWithSubscription: 0,
      contractsEnabled: false,
      contractPrice: 0,
      contractCustomers: 0,
    },
  });

  const handleInputChange = (section, values) => {
    setInputValues({ ...inputValues, [section]: { ...inputValues[section], ...values } });
  };

  const calculateBreakEven = () => {
    const totalInitialInvestment = Object.values(inputValues.initialInvestment).reduce((acc, val) => acc + parseFloat(val || 0), 0);
    const totalMonthlyOperationalCost = Object.values(inputValues.operationalCosts).reduce((acc, val) => acc + parseFloat(val || 0), 0);
    const totalMonthlyRevenue = (
      (parseFloat(inputValues.revenueStreams.individualSessions || 0) * parseFloat(inputValues.revenueStreams.individualCustomers || 0)) +
      (inputValues.revenueStreams.subscriptionsEnabled
        ? (parseFloat(inputValues.revenueStreams.subscriptionPrice || 0) * parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0)) +
          (parseFloat(inputValues.revenueStreams.avgSessionWithSubscription || 0) * parseFloat(inputValues.revenueStreams.avgCostWithSubscription || 0) * parseFloat(inputValues.revenueStreams.subscriptionCustomers || 0))
        : 0) +
      (inputValues.revenueStreams.contractsEnabled
        ? parseFloat(inputValues.revenueStreams.contractPrice || 0) * parseFloat(inputValues.revenueStreams.contractCustomers || 0)
        : 0)
    ).toFixed(2);

    const breakEvenMonths = (totalInitialInvestment / (totalMonthlyRevenue - totalMonthlyOperationalCost)).toFixed(2);
    const breakEvenWeeks = (breakEvenMonths * 4.33).toFixed(2);

    const individualSessionCustomersNeeded = (totalMonthlyOperationalCost / parseFloat(inputValues.revenueStreams.individualSessions || 1)).toFixed(2);
    const subscriptionCustomersNeeded = inputValues.revenueStreams.subscriptionsEnabled
      ? (totalMonthlyOperationalCost / (parseFloat(inputValues.revenueStreams.subscriptionPrice || 1) + (parseFloat(inputValues.revenueStreams.avgCostWithSubscription || 0) * parseFloat(inputValues.revenueStreams.avgSessionWithSubscription || 0)))).toFixed(2)
      : 0;
    const contractCustomersNeeded = inputValues.revenueStreams.contractsEnabled
      ? (totalMonthlyOperationalCost / parseFloat(inputValues.revenueStreams.contractPrice || 1)).toFixed(2)
      : 0;

    return {
      breakEvenMonths,
      breakEvenWeeks,
      individualSessionCustomersNeeded,
      subscriptionCustomersNeeded,
      contractCustomersNeeded,
    };
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold text-center mb-4">Mobile Dog Gym Calculator</h1>
      <InputForm onChange={handleInputChange} inputValues={inputValues} />
      <Results inputValues={inputValues} breakEvenData={calculateBreakEven()} />
    </div>
  );
};

export default Calculator;
